import React from "react"
import s from "./style/course-desc-file.module.css"

import { MdFilePresent, MdArchive } from "react-icons/md"

export default function CourseDescFile() {
    return (<div className={s.course_desc_file}>
        <p>berikut file yang bisa kamu unduh.</p>
        <div className={s.course_file_card}>
            <div className={s.course_file_icon}>
                <MdArchive/>
            </div>
            <div>
                <h5><a href="#">FILE ARCHIEVE.zip</a></h5>
                <p>diperbarui: 01-01-2023</p>
            </div>
        </div>
    </div>)
}