import React from 'react'

interface ButtonProp {
    className?:string, 
    type?: React.ButtonHTMLAttributes<HTMLButtonElement>['type'],
    text?:string, 
    icon?:string,
    onClick?:any,
    isDisabled?:boolean,
    isloading?:boolean
}

export default function Button({className, type, text, icon, onClick, isDisabled, isloading}:ButtonProp) {

text = text ? text : ''
    
return (
    <button className={'btn '+className+" "+(isDisabled ? 'disabled' : ''  )} type={type} onClick={onClick} disabled={isDisabled}>
        {
            isloading ? <div className='loading'>
                <div></div>
            </div> :
            <>{(icon ? <span className={icon}></span> : '')}{text}</>
        }
    </button>
)
}