import React from "react"
import CourseListItem from "./course-list-item"
import CourseDescFile from "./course-desc-file"

import s from "./style/course-list.module.css"

import { MdListAlt, MdOutlineFilePresent } from "react-icons/md"

export default function CourseList(){
    return(<div className={s.course_list}>
        <nav className={s.tabs} >
            <div className={s.tabs_item +" "+ s.active}>
                <MdListAlt/>
            </div>
            <div className={s.tabs_item}>
                <MdOutlineFilePresent/>
            </div>
        </nav>
        <section className={s.course_list_content}>
            <CourseDescFile />
            <CourseListItem id={0} parentid={0}/>
        </section>
        {/* <CourseListItem id={0} parentid={0}/>
        <CourseListItem id={1} />
        <CourseListItem id={2} /> */}
    </div>)
}