import React from "react"
import SideWindow from "./side-window"
import Button from "./button"
import { useAuth, AuthContextType } from "../Provider/AuthProvider"
import s from "./style/navbar.module.css"
import { Link } from "react-router-dom"

type NavbarProps = {
    children?: React.ReactNode,
    rightMost?: React.ReactNode
}

export default function Navbar(
    {children, rightMost} : NavbarProps
) {

    const [showSideMenu, setShowSideMenu] = React.useState<boolean>(false)

    const ToggleSideMenu = () => setShowSideMenu(!showSideMenu)

    const auth = useAuth() as AuthContextType

    return(<>
    <nav className={s.navbar} >
        {children}
        <div className={s.navbar_right}>
            {
                auth.user=== null ? 
                <>
                <Link to="/login"><Button className={s.btn_login} text="Masuk"/></Link>                
                <Link to="/register"><Button text="Daftar"/></Link>                
                </>
                :
                <div className={s.account} onClick={ToggleSideMenu}>
                    <span className={s.profil_picture}></span>
                    <span className={s.profil_nick}>@{auth?.user?.email}</span>
                </div>
            }
        </div>
    </nav>
    <SideWindow 
        hidden={!showSideMenu}
        toggle={ToggleSideMenu} />
    </>)
}