import React from "react"
import { Link } from "react-router-dom"
import { useForm } from 'react-hook-form'
import Layout from "./layout"

import { HiExclamation } from "react-icons/hi"

import { useAuth, AuthContextType, AuthState } from "../Provider/AuthProvider"
import Button from "../Component/button"

export default function Login(){

    const {register, handleSubmit, formState: { errors }} = useForm()

    const [showLoader, setShowLoader] = React.useState<boolean>(false)

    const auth = useAuth() as AuthContextType

    const HandleLogin = (data:Object) => {
        setShowLoader(true)
        auth.login(data as {email:string, password:string});
    }

    React.useEffect(() => {
        if(auth.authState == AuthState.LogInSuccess)
        {
            setTimeout(()=>setShowLoader(false),1000)
        }
        else if(auth.authState == AuthState.LogInFailed)
        {
            setTimeout(()=>setShowLoader(false),1000)
        }
    }, [auth.authState]);

return (
    <Layout>
    <section className="login">
        <button className="btn login-google-btn" type="button"
            onClick={auth.loginGoogle} >Masuk dengan google</button>
        <h4 className="login-divider">
            <span>Atau masuk menggunakan email</span>
        </h4>
        <div className="login-failed-alert" hidden={auth.authState !== AuthState.LogInFailed}>
            <p className="input-group-error"><HiExclamation/>Email atau kata sandi tidak sesuai, silahkan coba lagi.</p>
        </div>
        <form className="login-form" onSubmit={handleSubmit(HandleLogin)} >
            <div className="input-group">
                <label className="input-group-label">Email</label>
                <input className="input-group-input" type="text" placeholder="name@mail.com"
                    aria-invalid={errors.email ? "true" : "false"}
                    {...register("email", {required: true, pattern: /^\S+@\S+$/i})} />    
                    {errors.email ? <p className="input-group-error"><HiExclamation/>{errors.email?.ref?.value} harus berupa alamat surel yang valid.</p> : ''}
            </div>
            <div className="input-group">
                <label className="input-group-label">Kata Sandi</label>
                <input className="input-group-input" type="password" placeholder="****"
                    aria-invalid={errors.password ? "true" : "false"}
                    {...register("password", {required: true, minLength: 5})} />
                    {errors.password ? <p className="input-group-error"><HiExclamation/>minimal 5 karakter.</p> : ''} 
            </div>
            <Button className="btn login-submit-btn" type="submit" text="Masuk" isloading={showLoader} isDisabled={showLoader} />
        </form>        
        <h4 className="mv-0-7">atau <Link to="/" >Lupa Sandi</Link></h4>
        <div className="login-divider-daftar"></div>
        <h4 className="mv-0-7">Tidak punya akun? <Link to="/" >Daftar Sekarang</Link></h4>
    </section>
    </Layout>
)
}