import React from "react"
import s from "./style/course-video.module.css"

import { MdInfoOutline } from "react-icons/md";
import LikeDislike from "./like-dislike"

export default function CourseVideo(){
    return(
    <>
    <div className={s.course_video}>
        <p>video...</p>
        <button className={s.info_button} type="button">
            <MdInfoOutline/>
            <div className={s.info_button_pop}>
                chapter5. <br/> Integrasi mekanik dengan kontrol karakter
            </div>
        </button>
    </div>
    <div className={s.course_desc}>
        <div className="d-flex justify-between">
            <p className={s.course_desc_creator}>oleh admin uniXcademy</p>
            <div>
                <p className={s.course_desc_length}>vidio 30 menit 12 detik</p>
                <LikeDislike/>
            </div>
        </div>
        <h5 className={s.course_desc_chapter}>Bagian 5.</h5>
        <h3 className={s.course_desc_title}>Integrasi mekanik dengan kontrol karakter</h3>
    </div>
    </>)
}