import React from "react";
import s from "./style/course-list-item.module.css"

import { MdPlayCircleOutline, MdMessage, MdOutlineDescription, MdDone } from "react-icons/md"

export default function CourseListItem({id, parentid}:{id:number, parentid?:number}){

    return(
<>
        
    <div className={s.course_item_list_container} data-active={id===parentid}>
        <div className={s.course_item_title_container}>
            <h4 className={s.course_item_title} >01. Getting Started</h4>
            <span className={s.course_item_percentage}>100%</span>
        </div>
        <ul className={s.course_item_list} >
            <li>
                <span className={s.course_item_type}><MdPlayCircleOutline /></span>
                Setting Animator and Animation
                <span className={s.course_item_done}><MdDone/></span>
            </li>
            <li>
                <span className={s.course_item_type}><MdMessage /></span>
                Quiz 1
                <span className={s.course_item_done}><MdDone/></span>
            </li>
            <li>
                <span className={s.course_item_type}><MdOutlineDescription /></span>
                Difference between A and B.
                <span className={s.course_item_done}><MdDone/></span>
            </li>
        </ul>
    </div>
    
    <div className={s.course_item_list_container} data-active={id===parentid}>
        <div className={s.course_item_title_container}>
            <h4 className={s.course_item_title} >02. Getting Advanced</h4>
            <span className={s.course_item_percentage}>20%</span>
        </div>
        <ul className={s.course_item_list} >
            <li data-live={true} >
                <span className={s.course_item_type}><MdPlayCircleOutline /></span>
                Setting Animator and Animation
                <span className={s.course_item_est_time}>12 menit</span>
            </li>
            <li data-inactive={true} data-ispreview={true} >
                <span className={s.course_item_type}><MdMessage /></span>
                Quiz 1
                <span className={s.course_item_est_time}>12 menit</span>
            </li>
            <li data-inactive={true}>
                <span className={s.course_item_type}><MdOutlineDescription /></span>
                Difference between A and B.
                <span className={s.course_item_est_time}>12 menit</span>
            </li>
        </ul>
    </div>
</>)
}