import React from "react"
import { Link } from "react-router-dom"
import { useForm } from 'react-hook-form'
import Layout from "./layout"

import { HiExclamation, HiCheck } from "react-icons/hi"

import { useAuth } from "../Provider/AuthProvider"

export default function Register(){

    const {register, handleSubmit, formState: { errors, isDirty, isValid }} = useForm()

    const auth = useAuth()

return (
    <Layout>
    <section className="register">
        <button className="btn login-google-btn" type="button"
            >Masuk dengan google</button>
        <h4 className="login-divider">
            <span>Atau daftar menggunakan email</span>
        </h4>
        <form className="login-form" onSubmit={handleSubmit(auth.register)} >
            <div className="input-group">
                <label className="input-group-label">Nama</label>
                <input className="input-group-input" type="text" placeholder="nama kamu"
                    aria-invalid={errors.nama ? "true" : "false"}
                    {...register("nama", {required: true, pattern: /^[a-zA-Z]+$/})} />    
                    {errors.nama ? <p className="input-group-error"><HiExclamation/>harus tersusun dari abjad.</p> : ''}
            </div>
            <div className="input-group">
                <label className="input-group-label">Email</label>
                <input className="input-group-input" type="text" placeholder="name@mail.com"
                    aria-invalid={errors.email ? "true" : "false"}
                    {...register("email", {required: true, pattern: /^\S+@\S+$/i})} />    
                    {errors.email ? <p className="input-group-error"><HiExclamation/>{errors.email?.ref?.value} harus berupa alamat surel yang valid.</p> : ''}
            </div>
            <div className="input-group">
                <label className="input-group-label">Kata Sandi</label>
                <input className="input-group-input" type="password" placeholder="****"
                    aria-invalid={errors.password ? "true" : "false"}
                    {...register("password", {required: true, minLength: 5})} />
                    {errors.password ? <p className="input-group-error"><HiExclamation/>minimal 5 karakter.</p> : ''} 
            </div>
            <div className="input-group">
                <label className="input-group-label">FACTION</label>
                
            </div>
            <div className="register-term-of-service mv-0-7 d-flex">
                <input className="item-checkbox mr-0-5" type={"checkbox"}  {...register("agree", {required: true})}
                        aria-invalid={errors.agree ? "true" : "false"}>
                </input>
                <span>Saya menyetujui <Link to="#">Ketentuan Pengguna</Link> dan <Link to="#">Kebijakan Privasi</Link>.</span>
            </div>
            <button className="btn login-submit-btn" disabled={!isDirty || !isValid} type="submit" >Masuk</button>
        </form>        
        <div className="login-divider-daftar"></div>
        <h4 className="mv-0-7">Sudah punya akun? <Link to="/" >Masuk Sekarang</Link></h4>
    </section>
    </Layout>
)
}