import React from "react"
import CourseList from "../Component/course-list"
import CourseVideo from "../Component/course-video"
import Layout from "./layout"

export default function Course() {
return(<>
    <Layout>
        <div className="d-flex content-space">
            <section className="course-content" style={{flex: 1, position:"relative"}}>
                <CourseVideo />
            </section>
            <CourseList />
        </div>
    </Layout>
</>)
}