import React from "react"
import s from "./style/like-dislike.module.css"

import { MdOutlineThumbUpAlt, MdOutlineThumbDownAlt } from "react-icons/md"

export default function LikeDislike() {
    return(<div className={s.like_dislike}>
        <button type="button" className={s.like}>
            <MdOutlineThumbUpAlt/>
            {/* <span>like</span> */}
        </button>
        <button type="button" className={s.dislike}>
            <MdOutlineThumbDownAlt/>
            {/* <span>dislike</span> */}
        </button>
    </div>)
}