import React from "react"
import { HiBell, HiShoppingBag, HiChevronLeft } from "react-icons/hi"
import { MdPaid, MdOutlineTagFaces } from "react-icons/md"
import { CSSTransition } from 'react-transition-group';
import { AuthContextType, useAuth } from "../Provider/AuthProvider";

import s from "./style/side-window.module.css"

interface SideWindowProps {
    hidden: boolean,
    toggle: VoidFunction
}

export default function SideWindow(props:SideWindowProps){
    const nodeRef = React.useRef(null);

    const [show, setShow] = React.useState<boolean>(false)

    const auth = useAuth() as AuthContextType

    return(
    <CSSTransition
        in={!props.hidden}
        nodeRef={nodeRef}
        timeout={300}
        classNames="side-window"
        onEnter={() => setShow(true)}
        onExit={() => setShow(false)}
    >
    <div ref={nodeRef} className={s.side_window} id="side-window" hidden={!show}>
        <div className={s.back_button} onClick={props.toggle}>
            <div className={s.back}>
                <HiChevronLeft/>
            </div>
        </div>
        <div className={s.content_scroll}>
            <div className={s.button_icon}>
                <HiBell/>
                <span className={s.title} >Notifikasi</span>
            </div>
            <div className={s.button_icon}>
                <HiShoppingBag/>
                <span className={s.title} >Belanja</span>
            </div>
            <div className={s.button_icon}>
                <MdPaid/>
                <span className={s.title} >Top-up</span>
            </div>
            <div className={s.button_icon}>
                <MdOutlineTagFaces/>
                <span className={s.title} >Avatar</span>
            </div>
            <div className={s.button_icon} onClick={auth.logout}>
                <span className={s.title} >LOGOUT</span>
            </div>
        </div>
        <div className={s.side_window_bg} onClick={props.toggle}></div> 
    </div>
    </CSSTransition>
)
}