import React from "react"

import { 
    createUserWithEmailAndPassword, 
    signInWithEmailAndPassword,
    signInWithPopup, 
    GoogleAuthProvider,
    UserCredential
} from "firebase/auth"

import { auth, provider } from "../Firebase"

export interface AuthContextType {
  user : UserCredential | null,
  login : (data: {email:string, password:string}) => boolean,
  loginGoogle : () => void,
  logout: () => void,
  register: () => void,
  authState: AuthState,
}

export enum AuthState {
  None = 0,
  LoggingIn,
  LogInSuccess,
  LogInFailed,
}

const defaultGlobalState = {
  user : null,
  login : (data: {email:string, password:string}) => {},
  loginGoogle : () => {},
  logout: () => {},
  register: () => {},
  authState: AuthState.None,
}

const AuthContext = React.createContext(defaultGlobalState)



export default function AuthProvider({children}:{children:React.ReactNode}){

    const [user, setUser] = React.useState<UserCredential | null>(null)

    const [authState, setAuthState] = React.useState<AuthState>(AuthState.None)

    // initialization
    React.useEffect(()=>{
      const _user =  localStorage.hasOwnProperty('pengguna') ? JSON.parse(localStorage.getItem('pengguna') as string) : null
      setUser(_user)
    },[])

    // call this function when you want to authenticate the user
    const login = async (data:{email:string, password:string}) => {
        setAuthState(AuthState.LoggingIn)
        signInWithEmailAndPassword(auth, data.email, data.password)
        .then((userCredential) => {
            // Signed in 
            const user = userCredential.user;
            // ...
            setUser(userCredential)
            localStorage.setItem('pengguna', JSON.stringify(userCredential.user))
        
            setAuthState(AuthState.LogInSuccess)
          })
          .catch((error) => {
            const errorCode = error.code;
            const errorMessage = error.message;
        
            setAuthState(AuthState.LogInFailed)
          });
        
        // setUser('ada')
        // navigate("/profile");
    };

    // call this function to sign out logged in user
    const logout = () => {
        setUser(null)
        localStorage.removeItem('pengguna')
        // navigate("/", { replace: true });
    };

    const loginGoogle = () => {
        signInWithPopup(auth, provider)
        .then((result) => {
          // This gives you a Google Access Token. You can use it to access the Google API.
          const credential = GoogleAuthProvider.credentialFromResult(result);
          const token = credential?.accessToken;
          // The signed-in user info.
          const user = result.user;
          // IdP data available using getAdditionalUserInfo(result)
          // ...

          setUser(result)
          localStorage.setItem('pengguna', JSON.stringify(result.user))
      
        }).catch((error) => {
          // Handle Errors here.
          const errorCode = error.code;
          const errorMessage = error.message;
          // The email of the user's account used.
          const email = error.customData.email;
          // The AuthCredential type that was used.
          const credential = GoogleAuthProvider.credentialFromError(error);
          // ...
        });
      }

    const register = () => {

    }

    const value = React.useMemo(()=>({
        user,
        login,
        loginGoogle,
        logout,
        register,
        authState,
    }),[user, authState])

    return(
        <AuthContext.Provider value={value}>{children}</AuthContext.Provider>
    )
}

export const useAuth = () => React.useContext(AuthContext)